#starter-templates-ai-root {
  font-size: 16px;
  background: #fff;
}

html.wp-toolbar {
  padding: 0;
}

.starter-templates-onboarding #adminmenumain,
.starter-templates-onboarding #wpadminbar,
.starter-templates-onboarding #adminmenuback,
.starter-templates-onboarding #adminmenuwrap,
.starter-templates-onboarding #wpfooter {
  display: none;
}
.starter-templates-onboarding #wpcontent, .starter-templates-onboarding.auto-fold #wpcontent {
  margin: 0;
}
.starter-templates-onboarding.appearance_page_starter-templates #wpbody-content {
  padding: 0;
}

body * {
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 5px;
  background-color: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  border-radius: 0;
  background-color: #d8d8d8;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 0 rgba(0, 0, 0, 0.3);
  width: 20px;
}

a {
  color: #4361EE;
}
a:hover {
  color: #2D4AD1;
}

.step-row {
  background: #ffffff;
  display: flex;
  height: 100vh;
  overflow: hidden;
}

.step-col-right {
  width: 45%;
  padding: 0 2%;
}
.step-col-right .image-preview {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.step-col-right .image-preview img {
  max-height: calc( 100vh - 15em );
}

.step-col-left {
  width: 55%;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  height: calc( 100vh - 60px );
}

.step-content {
  background: #F4F5F6;
  padding: 6%;
  position: relative;
  margin: 20px 0 20px 20px;
  border-radius: 10px;
  flex: 1;
}

.step-actions {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  background: #fff;
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  width: 55%;
}
.step-actions p {
  font-size: 16px;
  color: #353852;
  margin: 0;
}

.customize-business-logo .ist-link {
  flex: 1;
  text-align: right;
}

.step-customizer .step-actions {
  right: 30px;
  left: 30px;
  max-width: 366px;
}
.step-customizer .step-col-left {
  width: 400px;
}
.step-customizer .step-col-right {
  padding: 0;
  width: calc( 100% - 400px );
}

.step-col-right img {
  max-width: 100%;
  height: auto;
}

.step-actions .dashicons-arrow-left-alt {
  margin-right: 5px;
}
.step-actions .dashicons-arrow-right-alt {
  margin-left: 5px;
}

.astra-sites-ai-logo-wrap {
  color: #3B3F5C;
}

.astra-sites-ai-logo-wrap,
.astra-sites-ai-rangecontrol-wrap {
  width: 100%;
}

.ist-logo {
  width: 70px;
  height: 75px;
  margin-bottom: 1em;
}

.ist-import-process-step-wrap {
  display: grid;
  grid-template-columns: 1fr 3fr;
  align-items: center;
  margin-bottom: 25px;
  grid-column-gap: 25px;
}

.ist-status-message-wrap p {
  color: gray;
  font-size: 1.2em;
}

.step-controls {
  padding: 10px;
}

.ist-progress-wrap {
  flex: 1;
  margin: 0px 50px;
  display: flex;
  align-items: center;
}
.ist-progress-wrap .ist-progress {
  flex: 1;
  background: #EAF1FF;
  border-radius: 5px;
  height: 8px;
}
.ist-progress-wrap .current-progress {
  background: #4361EE;
  border-radius: 5px;
  display: block;
  height: inherit;
  padding: 0;
  width: 0%;
}

.ist-circular-loader {
  max-width: 100px;
  max-height: 100px;
  position: relative;
}
.ist-circular-loader .ist-percent-wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  z-index: 3;
}
.ist-circular-loader .ist-svg-wrap {
  display: inline-block;
  position: relative;
  z-index: 1;
  width: 100%;
  height: auto;
  padding-bottom: 100%;
}
.ist-circular-loader .ist-svg-wrap svg {
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.ist-circular-loader .ist-svg-wrap svg circle {
  stroke-width: 6px;
  stroke: #e0e0e0;
  stroke-linecap: round;
}
.ist-circular-loader .ist-svg-wrap svg .ist-circular-bar-bg {
  fill: #ffffff;
}
.ist-circular-loader .ist-svg-wrap svg .ist-circular-bar {
  stroke: #50575e;
  transition: all 1s;
  fill: transparent;
}

/**
 * Customizer Contorls
 */
.customizer-controls {
  height: calc( 70vh - 230px );
  overflow-y: auto;
}

.astra-sites-ai-rangecontrol-wrap .components-range-control__wrapper {
  margin-left: 0;
}

.powered-by-google-logo {
  position: absolute;
  right: 30px;
  bottom: 20px;
}

.spinner-wrap {
  padding: 5em 0;
}

.ist-button.ist-button-outline {
  background: transparent;
  color: #4361EE;
  border: 1px solid #4361EE;
  width: 100%;
}
.ist-button.ist-button-outline:hover {
  background: #2D4AD1;
  color: #fff;
  border-color: #2D4AD1;
}

#google-place-search {
  background: transparent;
}

#google-place-search,
#google-place-search:focus {
  border: none;
  outline: none;
  box-shadow: none;
}

.business-name input[type=text], .business-name input[type=email], .business-address input[type=text], .business-address input[type=email], .business-email input[type=text], .business-email input[type=email], .business-phone input[type=text], .business-phone input[type=email] {
  border: unset;
}
.business-name .MuiOutlinedInput-input, .business-address .MuiOutlinedInput-input, .business-email .MuiOutlinedInput-input, .business-phone .MuiOutlinedInput-input {
  padding: 12px;
}
.business-name .MuiOutlinedInput-multiline, .business-address .MuiOutlinedInput-multiline, .business-email .MuiOutlinedInput-multiline, .business-phone .MuiOutlinedInput-multiline {
  background: #ffffff;
}
.business-name .MuiOutlinedInput-inputMultiline, .business-address .MuiOutlinedInput-inputMultiline, .business-email .MuiOutlinedInput-inputMultiline, .business-phone .MuiOutlinedInput-inputMultiline {
  padding-left: 0;
}
.business-name .MuiInputBase-input:focus, .business-address .MuiInputBase-input:focus, .business-email .MuiInputBase-input:focus, .business-phone .MuiInputBase-input:focus {
  box-shadow: none;
}